<template>
  <div>
    <!-- 顶部组件 -->
    <header-nav></header-nav>
    <!-- 轮播图 -->
    <div class="banner">
      <div class="imgs">
        <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
          <van-swipe-item v-for="(item, index) in imgs" :key="index">
            <img :src="item.pic" />
          </van-swipe-item>
        </van-swipe>
      </div>
    </div>

    <!-- 推荐歌单 -->
    <div class="remLists">
      <div class="title">
        推荐歌单
        <span>
          更多
          <i class="iconfont icon-jiantouyou"></i>
        </span>
      </div>
      <div class="content">
        <ul>
          <li v-for="item in Personalizedr" :key="item.id">
            <!-- <div class="img"> -->
            <router-link tag="div" :to="'/songlist?id=' + item.id" class="img">
              <img :src="item.picUrl" />
            </router-link>
            <!-- </div> -->
            <p>{{ item.name }}</p>
            <span
              ><i class="iconfont icon-erji"></i
              >{{ item.playCount | playCountData }}</span
            >
          </li>
        </ul>
      </div>
    </div>

    <!-- 最新的mv -->
    <div class="mvLists" v-show="mvdata">
      <div class="title">推荐视频</div>
      <van-grid :border="false" :column-num="3">
        <van-grid-item class="img" v-for="item in mvdata" :key="item.data.vid">
          <van-image
            class="imgs"
            @click="play(item.data.vid)"
            :src="item.data.coverUrl"
            ><van-icon class="play" name="play-circle-o" />
          </van-image>
          <p>{{ item.data.title }}</p>
        </van-grid-item>
      </van-grid>
    </div>

    <!-- 推荐歌单 -->
    <div class="remLists">
      <div class="title">最新专辑</div>
      <div class="contents">
        <!-- Swiper -->
        <div class="swiper-container swiper-container-01">
          <div class="swiper-wrapper">
            <div
              class="swiper-slide item"
              v-for="item in latestalbum"
              :key="item.id"
            >
              <!-- <div class="img"> -->
              <router-link
                tag="div"
                :to="'/albumplaylist?id=' + item.id"
                class="img"
              >
                <img :src="item.blurPicUrl" />
              </router-link>
              <!-- </div> -->
              <p>{{ item.name }}</p>
            </div>
          </div>
          <div class="swiper-pagination swiper-pagination-01"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getBanner, getPersonalizedr, getlatestalbum } from "../api/recommend";
import { getnewmv } from "../api/base";
import headerNav from "../components/base/headerNav.vue";
// https://3.swiper.com.cn/demo/index.html
import Swiper from "swiper";

export default {
  data() {
    return {
      imgs: null,
      Personalizedr: null,
      latestalbum: null,
      mvdata: null,
    };
  },
  methods: {
    play(id) {
      this.$router.push({
        name: "videoplay",
        params: {
          id: id,
        },
      });
      // this.$router.push('/videoplay').catch(() => {})
    },
    getBannerFun() {
      getBanner().then((data) => {
        this.imgs = data.banners;
      });
    },
    getPersonalizedrFun() {
      getPersonalizedr().then((data) => {
        this.Personalizedr = data.result;
      });
    },
    getlatestalbumFun() {
      getlatestalbum().then((data) => {
        this.latestalbum = data.albums;
      });
    },
    setId(id) {
      // 播放全部歌曲
      this.$emit("get-play-all-id", this.$route.query.id); //传递歌单id
      this.$emit("get-player-id", id);
    },
    getnewmvFun() {
      getnewmv().then((data) => {
        // console.log(data.datas);
        this.mvdata = data.datas;
      });
    },
  },
  created() {
    this.getBannerFun(); //获取轮播图
    this.getPersonalizedrFun(); //获取推荐歌单
    this.getlatestalbumFun(); //获取最新专辑
    this.getnewmvFun(); //获取推荐视频
  },
  components: {
    headerNav,
  },
  // 过滤器
  filters: {
    playCountData(value) {
      let w = value > 10000 ? value / 10000 : value; // 万计算
      let y =
        w > 10000 ? parseInt(value / 100000000) + "亿" : parseInt(w) + "万"; //亿计算
      return y; //1001762820
    },
  },
  mounted() {
    new Swiper(".swiper-container-01", {
      // pagination: '.swiper-pagination-01',
      slidesPerView: 3, //显示个数
      paginationClickable: true,
      observer: true,
      observeParents: true,
      spaceBetween: 15, //div之间间距
      // centeredSlides: true,
      slidesOffsetBefore: 10,
    });
  },
};
</script>

