<template>
  <div v-if="SongDetail">
    <!-- 全屏播放器 -->
    <div class="player" v-show="showPlayer">
      <div
        class="bg"
        :style="'background-image: url(' + SongDetail.al.picUrl + ')'"
      ></div>
      <div class="shade"></div>
      <div class="view">
        <div class="button" @click.stop="showPlayer = false">
          <van-icon name="arrow-down" />
        </div>

        <div class="title">
          <h2>{{ SongDetail.name }}</h2>
          <h3>
            <span v-for="(item, index) in SongDetail.ar" :key="index"
              >{{ item.name }}&nbsp;</span
            >
          </h3>
        </div>

        <div class="content">
          <div
            v-show="!togglelyric"
            @click="togglelyric = true"
            :class="['img', 'play', { pause: iconplay }]"
          >
            <img :src="SongDetail.al.picUrl" alt="" />
          </div>
          <div class="lyrics" v-show="togglelyric" @click="togglelyric = false">
            <ul :style="{ top: lyricTop }">
              <li
                v-for="(item, index) in lyric"
                :key="index"
                :class="{ con: currentRow == index }"
              >
                {{ item.text }}
              </li>
            </ul>
          </div>
        </div>

        <div class="range">
          <div class="top">
            <van-icon name="comment-o" @click="commentshows" />
            <van-icon
              name="contact"
              :class="{ red: asd != -1 }"
              @click="clicklove"
            />
          </div>
          <div class="bar">
            <span class="timeLeft">{{ timeLeft | playerTime }}</span>
            <van-slider
              @change="sliderchange"
              v-model="value"
              inactive-color="#514E4C"
              bar-height="4px"
              active-color="#ee0a24"
            >
              <template #button>
                <div class="custom-button"></div>
              </template>
            </van-slider>
            <span class="timeRight">{{ timeRight | playerTime }}</span>
          </div>
        </div>
        <div class="buttons">
          <i
            v-if="playerType == 1"
            @click="changeType"
            class="iconfont icon-24gl-repeat2"
          ></i>
          <i v-else @click="changeType" class="iconfont icon-24gl-shuffle"></i>
          <i class="iconfont icon-jiantou_shangyiye" @click="startup"></i>
          <van-icon
            class="big"
            :name="iconplay ? 'play-circle-o' : 'pause-circle-o'"
            @click.stop="toggleplay"
          />
          <!-- <van-icon name="pause-circle-o" /> -->
          <i class="iconfont icon-jiantou_xiayiye" @click="lowerFun"></i>
          <van-icon name="wap-nav" @click="showPopup" />
        </div>
      </div>
    </div>

    <!-- 迷你播放器 -->
    <div class="mini-player">
      <div
        :class="['img', 'play', { pause: iconplay }]"
        @click="showPlayer = true"
      >
        <img :src="SongDetail.al.picUrl" alt="" />
      </div>
      <div class="text">
        <h2>{{ SongDetail.name }}</h2>
        <h3>
          <span v-for="(item, index) in SongDetail.ar" :key="index"
            >{{ item.name }}&nbsp;</span
          >
        </h3>
      </div>
      <van-circle
        v-model="currentRate"
        :stroke-width="40"
        :rate="0"
        layer-color="rgb(81, 78, 76)"
        color="red"
        size="30px"
      >
        <van-icon @click.stop="toggleplay" v-show="iconplay" name="play" />
        <van-icon @click.stop="toggleplay" v-show="!iconplay" name="pause" />
      </van-circle>
      <van-icon name="wap-nav" @click="showPopup" />
    </div>

    <!-- 播放列表 -->
    <van-popup
      class="playlist"
      v-model="show"
      round
      position="bottom"
      :style="{ height: '50%' }"
    >
      <template>
        <div class="playlist">
          <div class="head">
            <i v-if="playerType == 1" class="iconfont icon-24gl-repeat2"></i>
            <i v-else class="iconfont icon-24gl-shuffle"></i>
            <span>{{ playmode }}(共{{ list.length }}首)</span>
            <van-icon
              name="arrow-down"
              class="backtrack"
              @click="show = false"
            />
          </div>
          <ul>
            <li v-for="(item, index) in list" :key="index">
              <div
                :class="[
                  'icon-left',
                  {
                    con: playshow == index,
                  },
                ]"
              >
                <i class="iconfont icon-a-zhengzaibofang4"></i>
              </div>
              <div class="info" @click="clickplay(item.id)">
                <h2 :class="{ con: playshow == index }">{{ item.name }}</h2>
                <h3 :class="{ con: playshow == index }">
                  <span v-for="value in item.ar" :key="value.id"
                    >{{ value.name }}&nbsp;</span
                  >
                </h3>
              </div>
              <div class="icon-right">
                <van-icon
                  class="right"
                  name="cross"
                  @click="songDelete(index)"
                />
              </div>
            </li>
          </ul>
        </div>
      </template>
    </van-popup>

    <!-- 评论 -->
    <van-popup
      v-model="commentshow"
      closeable
      position="bottom"
      :style="{ height: '100%' }"
    >
      <template>
        <div class="Comment">
          <div class="top">
            <div class="img">
              <img :src="SongDetail.al.picUrl" alt="" />
            </div>
            <h2>
              {{ SongDetail.name }}-<span
                v-for="(item, index) in SongDetail.ar"
                :key="index"
                >{{ item.name }}&nbsp;</span
              >
            </h2>
          </div>
          <div class="cont">
            <van-tabs v-model="active">
              <van-tab title="最热">
                <ul>
                  <li v-for="item in hotComments" :key="item.commentId">
                    <div class="img">
                      <img :src="item.user.avatarUrl" alt="" />
                    </div>
                    <div class="info">
                      <h2>{{ item.user.nickname }}</h2>
                      <h3>{{ item.timeStr }}</h3>
                      <div>
                        <p>
                          {{ item.content }}
                        </p>
                      </div>
                    </div>
                    <div class="icon">
                      <span>{{ item.likedCount }}</span>
                      <van-icon
                        name="good-job-o"
                        @click="like(item.commentId)"
                      />
                    </div>
                  </li>
                </ul>
              </van-tab>
              <van-tab title="最新">
                <ul>
                  <li v-for="item in comments" :key="item.commentId">
                    <div class="img">
                      <img :src="item.user.avatarUrl" alt="" />
                    </div>
                    <div class="info">
                      <h2>{{ item.user.nickname }}</h2>
                      <h3>{{ item.timeStr }}</h3>
                      <div>
                        <p>
                          {{ item.content }}
                        </p>
                      </div>
                    </div>
                    <div class="icon">
                      <span>{{ item.likedCount }}</span>
                      <van-icon
                        name="good-job-o"
                        @click="like(item.commentId)"
                      />
                    </div>
                  </li>
                </ul>
              </van-tab>
            </van-tabs>
          </div>
          <van-field
            class="mess"
            v-model="message"
            rows="1"
            autosize
            label="留言"
            type="textarea"
            placeholder="请输入留言"
          >
            <template #button>
              <van-button size="mini" type="primary" @click="sendcomment"
                >发表</van-button
              >
            </template>
          </van-field>
        </div>
      </template>
    </van-popup>

    <audio
      ref="audio"
      :src="musicData"
      @loadeddata="loadeddataFun"
      @ended="endedFun"
    ></audio>
  </div>
</template>

<script>
import {
  getSongUrl,
  getSongDetail,
  getLyric,
  getcomment,
  getlikes,
  getsendcomment,
} from "../../api/base";
import { getsublist, getsub } from "../../api/userinfo";
import { getPlaylistDetail } from "../../api/rank";
import { Toast } from "vant";
// import { Popup } from "vant";
export default {
  props: ["playerId", "listId", "playlistdata"],
  data() {
    return {
      musicData: "",
      iconplay: true, //播放按钮
      SongDetail: null, //歌曲信息
      value: 0, //滚动条 0-100
      currentRate: 0,
      showPlayer: false, //全屏播放器状态
      togglelyric: false, //歌词切换
      timeRight: 0, //右侧时间
      timeLeft: 0, //左侧时间
      clearset: null, //定时器
      playerLists: [], //播放列表
      lyric: [], //歌词数组
      lyricTop: "250px", //歌词位置
      currentRow: 0, //歌词高亮
      playerType: 1, //播放類型 1.列表播放 2.随机播放
      show: false, //显示播放列表
      playmode: "顺序播放", //播放模式
      list: [], //播放列表数据
      playshow: null, //播放列表高亮
      commentshow: false, //评论
      active: 0,
      comments: [], //最新
      hotComments: [], //最热
      message: "",
      favoritesingerId: [], //收藏歌手的id
      asd: -1,
    };
  },
  methods: {
    showPopup() {
      this.show = true;
    },
    //收藏歌手
    clicklove() {
      getsub({ id: this.SongDetail.ar[0].id, t: 1 }).then(() => {
        console.log("收藏成功");
      });
    },
    //取消收藏
    dblclicklove() {
      getsub({ id: this.SongDetail.ar[0].id, t: 0 }).then(() => {
        console.log("取消收藏");
      });
    },
    //获取歌曲的url
    getSongUrlFun() {
      getSongUrl({ id: this.playerId }).then((data) => {
        this.musicData = data.data[0].url;
        this.iconplay = true;
      });
    },

    //获取歌曲信息
    getSongDetailFun() {
      getSongDetail({ ids: this.playerId }).then((data) => {
        console.log(data);
        this.SongDetail = data.songs[0];
        this.playshow = this.list.findIndex(
          (item) => item.id == data.songs[0].id
        );
      });
      getcomment({ id: this.playerId }).then((data) => {
        // console.log("最新:", data.comments);
        // console.log("热评:", data.hotComments);
        this.hotComments = data.hotComments;
        this.comments = data.comments;
      });

      //获取收藏的歌手
      getsublist().then((data) => {
        console.log(data);
        this.favoritesingerId = data.data;
      });
    },

    // 获取歌曲歌词
    getLyricFun() {
      getLyric({ id: this.playerId }).then((data) => {
        this.formatLyric(data.lrc.lyric);
      });
    },

    // 处理歌词结构
    formatLyric(text) {
      this.lyric = [];
      let arr = text.split("\n"); //把原歌词切割成字符串
      let row = arr.length; //获取数组长度
      for (let i = 0; i < row; i++) {
        let temp_row = arr[i]; //[00:00.000] 作词 : MK王逸杰
        let temp_arr = temp_row.split("]"); //[00:00.000 , 作词 : MK王逸杰
        let lyrictext = temp_arr.pop(); //删除数组最后一个,返回值
        temp_arr.forEach((el) => {
          let obj = {};
          // [00:00.000 => [00,00.000]
          let time_arr = el.substr(1, el.length - 1).split(":");
          let s = time_arr[0] * 60 + Math.ceil(time_arr[1]); //把分钟变成秒   Math.ceil()
          obj.text = lyrictext;
          obj.time = s;
          if (lyrictext.length > 0) {
            this.lyric.push(obj); //每一行歌词处理后放入数组中
          }
        });
      }
    },

    // 播放暂停音乐
    toggleplay() {
      if (this.iconplay) {
        this.$refs.audio.play(); //播放音乐

        clearInterval(this.clearset); //清除定时器
        this.clearset = setInterval(() => {
          let currentTime = this.$refs.audio.currentTime; //实时变化时间
          let duration = this.$refs.audio.duration; //总时间
          this.timeLeft = currentTime;
          // 计算滚动条值
          this.currentRate = this.value = (currentTime / duration) * 100;
          if (this.value == 100) {
            clearInterval(this.clearset);
            this.$refs.audio.pause(); //暂停音乐
            this.iconplay = true;
          }
        }, 999);
      } else {
        this.$refs.audio.pause(); //暂停音乐
        clearInterval(this.clearset);
      }

      this.iconplay = !this.iconplay; //切换页面
    },

    // 音乐加载完成后,获取音乐数据
    loadeddataFun() {
      this.toggleplay(); //自动播放
      let duration = this.$refs.audio.duration;
      this.timeRight = duration;
      if (this.list.length == 0) {
        this.playerLists.forEach((item, index) => {
          this.list[index] = item;
        });
      }
    },

    // 音乐播放结束后
    endedFun() {
      this.iconplay = true;
      this.lowerFun(); //下一首歌
      // // 根据播放音乐类型,选择下一首播放
      // if (this.playerType == 1) {
      //   // 列表播放
      //   console.log(this.playerType);
      //   this.lowerFun(); //下一首歌
      // } else {
      //   // 随机播放
      //   let index = parseInt(Math.random() * this.list.length);
      //   this.$emit("set-player-id", this.playerLists[index].id);
      // }
    },

    // 获取滚动条变化
    sliderchange() {
      var duration = (this.$refs.audio.duration * this.value) / 100;
      this.timeLeft = duration; //修改页面数据
      this.$refs.audio.currentTime = duration; //修改播放时间
    },

    // 下一首歌
    lowerFun() {
      if (this.playerType == 1) {
        let id = this.SongDetail.id; // 获取id
        // 获取在播放歌曲列表中下标
        let index = this.list.findIndex((item) => item.id == id);
        // 1.获取当前歌曲下标,下标加一,下一首歌.如果最后一首歌,重置为零回到第一首歌
        index = index + 1 > this.list.length - 1 ? 0 : index + 1;
        // 返回父组件歌曲id
        this.$emit("set-player-id", this.list[index].id); //修改父组件id值
      } else {
        let index = parseInt(Math.random() * this.list.length);
        this.$emit("set-player-id", this.list[index].id);
      }
    },

    // 上一首歌
    startup() {
      if (this.playerType == 1) {
        let id = this.SongDetail.id; // 获取id
        // 获取在播放歌曲列表中下标
        let index = this.list.findIndex((item) => item.id == id);
        // 1.获取当前歌曲下标,下标减一,上一首歌.如果第一首歌,设置为最后一首歌
        index = index - 1 < 0 ? this.list.length - 1 : index - 1;
        // 返回父组件歌曲id
        this.$emit("set-player-id", this.list[index].id); //修改父组件id值
      } else {
        let index = parseInt(Math.random() * this.list.length);
        this.$emit("set-player-id", this.list[index].id);
      }
    },

    // 播放类型切换
    changeType() {
      this.playerType = this.playerType == 1 ? 2 : 1;
      this.playmode = this.playerType == 1 ? "顺序播放" : "随机播放";
      if (this.playerType == 1) {
        Toast("顺序播放");
      } else {
        Toast("随机播放");
      }
    },
    //删除播放列表的歌
    songDelete(index) {
      if (this.list.length == 1) {
        this.SongDetail = null;
        this.$refs.audio.pause(); //暂停音乐
        clearInterval(this.clearset);
      }
      this.list.splice(index, 1);
    },
    //点击播放
    clickplay(id) {
      let index = this.list.findIndex((item) => item.id == id);
      this.$emit("set-player-id", this.list[index].id); //修改父组件id值
    },
    //评论
    commentshows() {
      this.commentshow = true;
    },
    //点赞
    like(id) {
      getlikes({ id: id }).then(() => {
        Toast("点赞成功");
      });
    },
    //发表评论
    sendcomment() {
      if (this.message != "") {
        getsendcomment({ t: 1, content: this.message }).then(() => {
          Toast("发表成功");
        });
        this.message=""
      }
    },
  },

  // 侦听器,只要数据修改,重新执行
  watch: {
    // 侦听播放id
    playerId() {
      this.getSongUrlFun(); //歌曲url
      this.getSongDetailFun(); //歌曲信息
      this.getLyricFun(); //歌曲歌词
    },

    // 侦听播放列表id
    listId() {
      getPlaylistDetail({ id: this.listId }).then((data) => {
        this.playerLists = data.playlist.tracks; //获取歌单数据
        this.playerLists.forEach((item, index) => {
          this.list[index] = item;
        });
      });
    },

    // 左测歌时间变化
    timeLeft() {
      for (let i = 0; i < this.lyric.length; i++) {
        if (parseInt(this.timeLeft) <= this.lyric[i].time) {
          this.lyricTop = 250 - i * 36 + "px";
          this.currentRow = i; //歌词高亮
          break;
        }
      }
    },
    //监听添加播放列表数据
    playlistdata() {
      this.list.push(this.playlistdata);
    },
    SongDetail() {
      this.asd = this.favoritesingerId.findIndex(
        (item) => item.id == this.SongDetail.ar[0].id
      );
    },
  },
  filters: {
    playerTime(data) {
      let m = parseInt(data / 60); //分钟
      let s = parseInt(data % 60); //秒
      if (m == 0 && s == 0) {
        return "0:00";
      } else {
        return m + ":" + (s < 10 ? "0" + s : s); // 01
      }
    },
  },
};
</script>

