    </div>
  </div>
</template>

<script>
import {
  getUserinfo,
  getdailysignin,
  gettoday,
  getlogout,
} from "../../api/userinfo";
import { Toast } from "vant";
export default {
  data() {
    return {
      uid: null,
      level: null, //等级
      avatarUrl: null, //头像
      nickname: null, //用户名
      signInStatus: null, //签到状态 null已签到
      svipshow: 0,
      viptxte: 0,
    };
  },
  methods: {
    //用户详情
    getUserinfoFun() {
      getUserinfo({ uid: this.uid }).then((data) => {
        this.avatarUrl = data.profile.avatarUrl;
        this.nickname = data.profile.nickname;
        this.level = data.level;
        this.viptxte = this.svipshow = data.profile.vipType;
      });
    },
    //检查是否签到
    gettodayFun() {
      gettoday().then((data) => {
        if (data.message) {
          this.signInStatus = data.message;
        }
      });
    },
    //签到
    getdailysigninFun() {
      if (this.signInStatus == "已签到") {
        Toast("重复签到");
        return;
      }
      getdailysignin().then(() => {
        Toast("签到成功");
      });
    },
    //退出登入
    getlogoutFun() {
      if (this.uid == null) {
        return;
      }
      getlogout().then(() => {
        window.localStorage.clear();
      });

      this.$router.push("/login").catch(() => {});
      Toast("退出成功");
    },
  },
  created() {
    this.uid = window.localStorage.getItem("uid");
    this.gettodayFun();
    this.getUserinfoFun();
  },
};
</script>

