// 推荐接口
import request from "./http";
// http://192.168.110.2:3000/banner
// 获取轮播图数据
export function getBanner(){
    return request({
        url:"/banner",
        method:"get",
        params:{
            type:3
        }
    })
}

// 获取推荐歌单数据
export function getPersonalizedr(){
    return request({
        url:"/personalized",
        method:"get",
        params:{
            limit:6
        }
    })
}

// 获取最新专辑数据
export function getlatestalbum(){
    return request({
        url:"/album/newest",
        method:"get",
    })
}