
import request from "./http";

// 获取歌曲url
export function getSongUrl(params){
    return request({
        url:"/song/url",
        method:"get",
        params
    })
}
// 获取歌曲详情
// /song/detail?ids=347230
export function getSongDetail(params){
    return request({
        url:"/song/detail",
        method:"get",
        params
    })
}
