<template>
  <div id="app">
    <div :class="['main', { bottom: playerId }]">
      <!-- 路由视图 -->
      <router-view
        @get-player-id="getPlayerId"
        @get-play-all-id="getPlayAllId"
        @push="push"
      ></router-view>
      <!-- 播放器组件 -->
      <player
        :class="{ raise: playerId }"
        v-show="playerId"
        :player-id="playerId"
        :list-id="listId"
        @set-player-id="setPlayerId"
        :playlistdata="playlistdata"
      ></player>
    </div>
  </div>
</template>

<script>
import player from "./components/base/player.vue";
export default {
  data() {
    return {
      playerId: null,
      listId: null,
      currentRate: 0,
      raises: null,
      show: false,
      infodata: null,
      playlistdata: null,
    };
  },
  methods: {
    // 获取子组件播放歌曲id
    getPlayerId(id) {
      this.playerId = id;
    }, // 播放全部歌曲id
    getPlayAllId(id) {
      this.listId = id;
    },
    setPlayerId(id) {
      this.playerId = id;
    },
    //添加到播放列表传值
    push(id) {
      this.playlistdata = id;
    },
  },
  components: {
    player,
  },
  watch: {
    playerId() {},
  },
};
</script>

