import Vue from 'vue'
import VueRouter from 'vue-router'
import recommend from '../views/recommend.vue'

Vue.use(VueRouter)

const routes = [{
    path: "/",
    redirect: "/recommend"
  },
  {
    path: "/userInfo",
    name: "userInfo",
    component: () => import("../views/userinfo.vue"),
    beforeEnter: (to, from, next) => {
      let login = window.localStorage.getItem("token");
      // 判断没有值
      if (!login) {
        next("/login");
        return;
      } else {
        next();
      }
    }
  },
  {
    path: "/login",
    name: "login",
    component: () => import("../components/base/login.vue")
  },
  {
    path: "/recommend",
    name: "recommend",
    component: recommend
  },
  {
    path: "/singer",
    name: "singer",
    component: () => import("../views/singer.vue")
  },
  {
    path: "/rank",
    name: "rank",
    component: () => import("../views/rank.vue")
  },
  {
    path: "/search",
    name: "search",
    component: () => import("../views/search.vue")
  },
  {
    path: "/my",
    name: "my",
    component: () => import("../views/my.vue")
  },
  {
    path: "/songlist",
    name: "songlist",
    component: () => import("../views/songlist.vue")
  },
  {
    path: "/albumplaylist",
    name: "albumplaylist",
    component: () => import("../views/albumplaylist.vue")
  },
  {
    path: "/rank/ranklist",
    name: "ranklist",
    component: () => import("../components/rank/ranklist.vue")
  },
  {
    path: "/recentlyplayed",
    name: "recentlyplayed",
    component: () => import("../components/base/recentlyplayed.vue")
  },
  {
    path: "/videoplay",
    name: "videoplay",
    component: () => import("../views/videoplay.vue")
  },



]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router